<template>
  <div ref="scroll_wrap" class="list_wrap">
    <van-pull-refresh
      v-model="isRefreshLoading"
      :success-text="successText"
      @refresh="onRefresh"
      :success-duration="500"
    >
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <article-item
          v-for="(item, index) in recommend"
          :key="index"
          :article="item"
        ></article-item>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import { getUserArticles } from '@/api/home.js';

export default {
  name: 'ArticleList',
  props: {
    channel: {
      type: Object,
      required: true,
    },
  },

  components: {
    ArticleItem: () => import('@/components/article_item'),
  },
  data() {
    return {
      scrollTop: 0,
      successText: undefined,
      isRefreshLoading: false, // 下拉刷新是否处于加载中状态
      recommend: [], // 推荐数据
      timestamp: null, // 时间戳
      loading: false, // 触底自己变true 加载中的状态
      finished: false, // 请求完所有数据后 手动改为true 不再加载更多
    };
  },
  methods: {
    // 下拉刷新
    async onRefresh() {
      try {
        const { data } = await getUserArticles({
          channel_id: this.channel.id,
          timestamp: Date.now(),
          with_top: 1,
        });
        const { page, results, pre_timestamp } = data.data;

        this.recommend.unshift(...results);
        this.isRefreshLoading = false;
      } catch (e) {
        console.log(e);
      }
    },
    // 触底获取数据
    async onLoad() {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      try {
        const { data } = await getUserArticles({
          channel_id: this.channel.id,
          timestamp: this.timestamp || Date.now(),
          with_top: 1,
        });
        const { page, results, pre_timestamp } = data.data;
        this.successText = `更新了${results.length}条数据`;
        this.recommend.push(...results);
        this.loading = false;
        if (results.length) {
          this.timestamp = pre_timestamp;
        } else {
          this.finished = true; // 不再触发加载更多
        }
      } catch (e) {
        console.log(e);
      }
    },
  },
  mounted() {
    const scrollBox = this.$refs.scroll_wrap;
    scrollBox.addEventListener('scroll', (e) => {
      this.scrollTop = e.target.scrollTop;
    });
  },
  activated() {
    this.$refs.scroll_wrap.scrollTop = this.scrollTop;
  },
  deactivated() {},
};
</script>

<style lang="less" scoped>
.list_wrap {
  position: fixed;
  left: 0;
  right: 0;
  top: 90px;
  bottom: 50px;
  overflow: scroll;
}
.van-tabs__content {
  .van-pull-refresh {
  }
}
</style>
